*{
  color: cornflowerblue;
}


.container{
  width:30%;
  display:flex;
  flex-direction: column;
  height: fit-content;
  background-color: cornsilk;
}
.container h1{
  color: cornflowerblue;
}
.flex{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.list{
  display: flex;
  background-color: rgb(242, 195, 228);
  padding:2%;
  margin:1% 1.5%;
  border-radius: 10px;
}
.list img{
  height: 60px;
  width:60px;
  border-radius: 100%;
  /* padding: 5px; */
}
.list h3,h5{
  margin:0px 10px;
}
button{
  padding: 3% 0;
  margin:2% 3%;
  color: white;
  background-color: cornflowerblue;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}